.hero {
    min-height: 100vh;
    min-width: 100vw;
    color: white;
    display: flex;
    flex-direction: column;
    text-align: center;
    position: relative;
    overflow: hidden;
}

.whatsapp {
  height: 3rem;
  width: 3rem;
  position: fixed;
  right: 3rem;
  bottom: 2rem;
}


.bg-img {
  position: absolute;
  top: -18rem;
  left: 0;
  pointer-events: none;
}

.heading {
    margin-top: 10rem;
}

.hero-txt {
    font-size: 4rem;
    line-height: 1.3;
}

.hero-txt span .word .char{
    font-family: "Bricolage", sans-serif;
    font-size: 4rem;
  }

.video1 {
    width: 60rem;
}

.video2 {
    width: 40rem;
    object-fit: cover;
}

.video-container {
    display: inline-block;
    width: 30rem;
    height: 3.5rem;
    overflow: hidden;
    border-radius: 50px;
  }

  .vid-cont1 {
    margin-left: 2rem;
  }

  .vid-cont2 {
    margin-right: 2rem;
  }

.subhead {
    margin-top: 1.5rem;
    font-family: "Poppins", sans-serif;
    font-size: 1.2rem;
    font-weight: 400;
}

/* contact us button */
.cnt-button {
    margin-top: 2rem;
    background: transparent;
    color: #fff;
    font-family: "Poppins", sans-serif;
    font-weight: 600;
    border-radius: 0.5rem;
    font-size: 1.1rem;
    padding-left: 2rem;
    padding-right: 2rem;
    padding-top: 0.7rem;
    padding-bottom: 0.7rem;
    cursor: pointer;
    text-align: center;
    margin-right: 0.5rem;
    display: inline-flex;
    align-items: center;
    border: 1px solid #befd92;
    margin-bottom: 4rem;
  }
  
  .cnt-button:hover {
    background-color: #befd92;
    color: #111010d1;
  }

  .trust-txt {
    color: #ffffff80;
    position: absolute;
    bottom: 2rem;
    left: 50%;
    transform: translateX(-50%);
  }
  
  @media screen and (max-width: 600px) {
  .hero {
    min-height: 100vh;
  }

  .bg-img {
    left: -10rem;
  }

    .hero-txt {
      font-size: 2rem;
  }

  .video-container {
    display: inline-block;
    width: 8rem;
    height: 1.5rem;
  }

  .video1 {
    width: 20rem;
}

.vid-cont1 {
  margin-left: 0.5rem;
}

.vid-cont2 {
  margin-right: .5rem;
}

.heading {
  margin: auto 0;
}

.hero-txt span .word .char{
  font-size: 1.5rem;
}


.subhead {
  margin-top: 1.5rem;
  font-size: .8rem;
  font-weight: 400;
}

.cnt-button  {
  margin-top: 3rem;
  font-size: 1rem;
    padding: 0.2rem .8rem;
}

.trust-txt{
  bottom: 6rem;
}

  }

