.about {
    min-height: 100vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    overflow: hidden;
    position: relative;
}

.about * {
    font-family: "Poppins", sans-serif;
}

.about-sec {
    margin-top: 10rem;
    width: 70%;
    overflow: hidden;
}

.about-sec h2 {
    margin-top: 4rem;
    color: #fff;
    text-align: center;
}

.about-head {
    text-align: center;
    color: #fff;
}

.about-sec p {
    font-size: 1.3rem;
    line-height: 2;
    text-align: center;
    margin-top: 1.5rem;
    color: #fff;
}

.round {
    position: absolute;
    top: -13px;
    left: 5px;
    height: 4.6rem;
    width: 8.7rem;
    z-index: -1; 
}

.highlight {
    position: relative;
    display: inline-block;
    padding-left: .9rem;
    padding-right: .6rem;
}

.path { 
    stroke-dasharray: 500;
    stroke-dashoffset: 500;
}

.animate {
    animation: draw 2s forwards;
}

@keyframes draw {
    to {
        stroke-dashoffset: 0;
    }
}

@media screen and (max-width: 600px) {
    .about-head {
        font-size: 2rem;
        text-align: left;
    }

    .about-sec p {
        font-size: 1.1rem;
        line-height: 1.6;
        text-align: left;
        align-items: center;
    }

    .about-sec {
        width: 90%;
    }

    .about-sec h2 {
        text-align: left;
    }

    .round {
        top: -17px;
        left: -3px;
        height: 3.7rem;
        width: 9rem;
    }
}