#Layer_1 {
    fill: white;
    height: 37px;
    width: 37px;
}

.header {
    height: 60px;
    width: 80%;
    padding: 0 30px;
    display: flex;
    color: white;
    align-items: center;
    justify-content: space-between;
    background-color: rgba(255, 255, 255, 0.173);
    border-radius: 13px;
    position: absolute;
    top: 2rem;
    z-index: 1;
}

.header > ul {
    display: flex;
    gap: 8rem;
    align-items: center;
    justify-content: space-evenly;
    list-style-type: none;

}

.header > ul > li >  a {
    text-decoration: none;
    font-size: 1.1rem;
    color: white;
}

.header ul li {
    cursor: pointer;
  }

  .header ul li a:hover {
    color: #BEFD92;
  }

  .contact-btn {
    padding: 5px;
    border-radius: 7px;
    font-size: 1rem;
    background: transparent;
    border: 1px solid #fff;
    color: #fff;
    cursor: pointer;
  }

  .contact-btn:hover {
    border-color: #BEFD92;
    color: #BEFD92;
  }

  .hamburger div{
    width: 30px;
    height:3px;
    background: #f2f5f7;
    margin: 5px;
    transition: all 0.3s ease;
}
.hamburger{
    display: none;
}

  @media screen and (max-width: 600px) {
    .header {
      top: 0;
      padding: 0;
      height: 60px;
      width: 100%;
      position: fixed;
      border-radius: 0;
      background-color: #0f0f0f;
    }

    
    .header ul {
    gap: 0;
    }

    .contact-btn {
      display: none;
    }

    .logo {
      margin-left: 1.5rem;
    }

    .hamburger{
      display:block;
      position: absolute;
      cursor: pointer;
      right: 5%;
      top: 50%;
      transform: translate(-5%, -50%);
      z-index: 2;
      transition: all 0.7s ease;
  }

  .nav-links{
    position: fixed;
    top: -100%;
    width: 100%;
    background: #0f0f0f;
    height: 50vh;
    width: 400px;
    flex-direction: column;
    transition: all 1s ease-out;
    pointer-events: none;
    text-align: center;
}

.nav-links li {
  margin: 16px 0;
}

.nav-links.open{
    top: 0;
    pointer-events: all;
}
.nav-links li{
    opacity: 0;
}

li.fade{
    opacity: 1;
}
}

  .toggle .line1{
    transform: rotate(-45deg) translate(-5px,6px);
}
.toggle .line2{
    transition: all 0.7s ease;
    width:0;
}
.toggle .line3{
    transform: rotate(45deg) translate(-5px,-6px);
}