.footer {
    padding: 4rem 0 1rem 0;
    background: linear-gradient(to right, #ffeeee, #e5f2cc);
    width: 100%;
    border-radius: 3rem 3rem 0 0;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}
.footer * {
    font-family: "Poppins", sans-serif;
    text-decoration: none;
    font-weight: 400;
}

.footer-cont {
    display: flex;
    gap: 20rem;
}

.connections {
    width: 17rem;
}

.connections > div {
    margin-bottom: 1rem;
    display: flex;
    gap: 8px;
}

.connections > div > img {
    height: 1.3rem;
    width: 1.3rem;
}

.footer-head {
    font-weight: 600;
    margin-bottom: 1rem;
}

.navi-links li {
    list-style-type: none;
    margin-bottom: .8rem;
}

.navi-links li a {
    color: #000;
}

.navi-links li a:hover {
    color: #551A9A;
}

.location p span {
    text-wrap: nowrap;
}

.copyright {
    color: #505050;
    text-align: center;
}

@media screen and (max-width: 600px) {
    .footer {
        border-radius: 1.5rem 1.5rem 0 0;
    }

    .footer-cont {
        flex-direction: column;
        gap: 2rem;
    }

    .copyright {
        margin-top: 2rem;
        font-size: .8rem;
    }

    .connections {
        position: relative;
    }

    .connections > div > img {
        position: absolute;
        left: -30px;
    }
}