.programming {
    padding: 0 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .programming h1 {
    color: #fff; 
    margin: 3rem 0 5rem 0;
    font-weight: 400;
  }
  
    .course-head h1:first-of-type{
      font-size: 2.8rem;
      margin: 5rem 0 0 0;
      font-weight: 600;
    }
  

  .card-container {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 5rem;
    justify-items: center;
  }

  .toggle-button {
    margin-top: 4rem;
    background: transparent;
    color: #fff;
    font-family: "Poppins", sans-serif;
    font-weight: 600;
    border-radius: 0.5rem;
    font-size: 1.1rem;
    padding-left: 2rem;
    padding-right: 2rem;
    padding-top: 0.7rem;
    padding-bottom: 0.7rem;
    cursor: pointer;
    text-align: center;
    display: inline-flex;
    align-items: center;
    border: 1px solid #befd92;
    margin-bottom: 3rem;
  }
  
  .toggle-button:hover {
    background-color: #befd92;
    color: #111010d1;
  }
  
  @media (max-width: 1024px) {
    .card-container {
      grid-template-columns: repeat(2, 1fr);
    }
  }
  
  @media (max-width: 768px) {
    .card-container {
      grid-template-columns: repeat(2, 1fr);
    }
  }

  @media screen and (max-width: 600px) {
    .cnt-button  {
      margin-top: 3rem;
      font-size: 1rem;
      padding: 0.2rem .8rem;
      margin-bottom: 2rem;
    }

    .card-container {
      gap: 2rem;
      grid-template-columns: repeat(1, 1fr);
    }

    .programming h1 {
      color: #fff; 
      font-size: 1.8rem;
      margin: 2rem 0 4rem 0;
      font-weight: 400;
    }
    
      .course-head h1:first-of-type{
        font-size: 2rem;
        margin: 3rem 0 0 0;
        font-weight: 600;
      }
  }
  