.contact-page {
  min-height: 100vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow: hidden;
  position: relative;
}

.cont-head {
  color: #fff;
  margin-top: 10rem;
}

.contact-div {
  margin-top: 6rem;
  display: flex;
  align-items: center;
  width: 70%;
  justify-content: space-around;
}

.contact-div * {
  color: #fff;
}

.cont-left {
  width: 40%;
}

.cont-right {
  width: 45%;
}

.cont-right > div {
  margin-bottom: 3rem;
  display: flex;
  gap: 8px;
}

.contact-form p {
  font-weight: 600;
  margin-bottom: 0.4rem;
}

.contact-form {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.mail a {
    text-decoration: none;
}

.contact-form input {
  margin-bottom: 1rem;
}

input {
  height: 2.3rem;
}

input[type="text"],
input[type="email"],
textarea[name="message"] {
  color: #000;
  resize: none;
}

.cont-right > div > img {
  height: 1.3rem;
  width: 1.3rem;
  filter: brightness(0) invert(1);
}

input,
textarea {
  width: 100%;
  padding: 12px;
  border-radius: 8px;
}

.cnt-btn {
  margin-top: 2rem;
  padding: 5px;
  border-radius: 7px;
  font-size: 1rem;
  background: transparent;
  color: #fff;
  cursor: pointer;
  border-color: #befd92;
  width: 6rem;
  margin-left: 50%;
  transform: translateX(-50%);
}

.cnt-btn:hover {
  color: #000;
  background-color: #befd92;
}

@media screen and (max-width: 600px) {
  .contact-div {
    margin-top: 3rem;
    flex-direction: column;
    width: 100%;
    gap: 3rem;
  }

  .cont-right > div {
    margin-bottom: 1rem;
  }

  .cont-left {
    width: 80%;
  }

  .cont-right {
    width: 80%;
  }
}
