.card {
  padding: 20px;
  width: 320px;
  min-height: 370px;
  border-radius: 20px;
  background: #212121;
  transition: 0.4s;
}

.card:hover {
  translate: 0 -10px;
}

.card-title {
  font-size: 18px;
  font-weight: 600;
  color: #b2eccf;
  margin: 15px 0 0 10px;
}

.card-image {
  min-height: 170px;
  width: 100%;
  background-color: #313131;
  border-radius: 15px;
  background: #313131;
}

.card-body {
  margin: 13px 0 0 10px;
  color: rgb(184, 184, 184);
  font-size: 15px;
  line-height: 1.3;
  font-family: 'poppins', sans-serif;
  font-weight: 400;
}